
import { defineComponent } from "vue";

import CarForm from "@/components/CarForm.vue";
import CarItem from "@/components/Car.vue";
import { Car } from "@/types/Car";
import { mapActions, mapGetters } from "vuex";

export default defineComponent({
    name: "EditCar",
    data() {
        return {
            selectedCar: null as null | Car,
            selectedCarToDelete: null as null | Car,
            validationError: {},
            error: false,
            success: false,
            isLoading: false,
        };
    },
    watch: {
        selectedCar() {
            this.error = false;
            this.validationError = {};
            this.success = false;
        },
        success(to: boolean) {
            if (to) setTimeout(() => (this.success = false), 5000);
        },
    },
    components: {
        CarItem,
        CarForm,
    },
    methods: {
        ...mapActions(["updateCar", "deleteCarFromList"]),
        ...mapActions("adminPanel", ["logout"]),
        setCar(car: Car) {
            this.selectedCar = Object.assign({}, car);
            this.selectedCar.images = [...this.selectedCar.images];
        },
        submit(data: Car) {
            this.isLoading = true;
            this.error = false;
            this.validationError = {};
            const id = data._id;
            fetch(`/api/car/${id}`, {
                method: "PUT",
                body: JSON.stringify(data),
                headers: {
                    "Content-Type": "application/json",
                    authorization: `Bearer ${this.token}`,
                },
            })
                .then((e) => e.json())
                .then((e) => {
                    if (e.auth === false) {
                        this.logout();
                        return;
                    }
                    if (e.validationError)
                        this.validationError = e.validationError;
                    if (e.success) {
                        this.updateCar(e.data);
                        this.success = true;
                    }
                })
                .catch((err) => {
                    this.error = true;
                })
                .finally(() => (this.isLoading = false));
        },
        sellCar(id: string) {
            fetch(`/api/car/${id}/sell`, {
                method: "PUT",
                headers: { authorization: `Bearer ${this.token}` },
            })
                .then((e) => e.json())
                .then((e) => {
                    if (e.success) {
                        this.updateCar(e.data);
                    }
                    if (e.auth === false) {
                        this.logout();
                    }
                });
        },
        deleteCar() {
            if (!this.selectedCarToDelete) return;

            const id = this.selectedCarToDelete._id;

            fetch(`/api/car/${id}`, {
                method: "DELETE",
                headers: {
                    authorization: `Bearer ${this.token}`,
                },
            })
                .then((e) => e.json())
                .then((e) => {
                    if (e.success) {
                        (this.$refs as any).modalCloseBtn.click();
                        this.deleteCarFromList(id);
                    }
                    if (e.auth === false) {
                        this.logout();
                    }
                });
        },
        reset() {
            this.error = false;
            this.validationError = {};
        },
    },
    computed: {
        ...mapGetters(["allCars"]),
        ...mapGetters("adminPanel", ["token"]),
    },
});
