
import { defineComponent } from "vue";

export default defineComponent({
    name: "ContactForm",
    data() {
        return {
            title: {
                value: "",
                error: "",
                min: 3,
                max: 35,
            },
            name: {
                value: "",
                error: "",
                min: 3,
                max: 25,
            },
            email: {
                value: "",
                error: "",
            },
            message: {
                value: "",
                error: "",
                min: 8,
                max: 2000,
            },
            success: false,
            isLoading: false,
            error: null as null | string,
        };
    },
    watch: {
        success(to: boolean) {
            if (to) setTimeout(() => (this.success = false), 5000);
        },
    },
    methods: {
        submit() {
            const errors = this.validateForm();
            if (errors) return;
            this.isLoading = true;
            this.error = null;
            this.success = false;

            fetch("/api/message", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                },
                body: JSON.stringify({
                    title: this.title.value,
                    name: this.name.value,
                    email: this.email.value,
                    message: this.message.value,
                }),
            })
                .then((e) => e.json())
                .then((e) => {
                    if (e.validationError) {
                        const { email, message, name, title } =
                            e.validationError;

                        if (title) this.title.error = title;
                        if (name) this.name.error = name;
                        if (email) this.email.error = email;
                        if (message) this.message.error = message;
                        return;
                    }
                    if (e.success) {
                        this.success = true;
                        this.title.value = "";
                        this.name.value = "";
                        this.email.value = "";
                        this.message.value = "";
                    }
                    if (e.error) {
                        this.error = "Something went wrong";
                    }
                })
                .catch((err) => {
                    this.error = "Something went wrong";
                })
                .finally(() => {
                    this.isLoading = false;
                });

            return;
        },
        onChange(name: "title" | "name" | "email" | "message") {
            console.log("Change: " + name);
            this[name].error = "";
        },
        validateMail() {
            if (!this.email.value.length) {
                this.email.error = "This field is required";
                return false;
            }
            const reg = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
            if (reg.test(this.email.value)) {
                this.email.error = "";
                return true;
            }
            this.email.error = "Email is not valid";
            return false;
        },
        validateForm() {
            let errorCount = 0;
            const { title, name, message } = this;
            const mail = this.validateMail();
            !mail && errorCount++;

            [title, name, message].forEach((e) => {
                if (!e.value.length) {
                    e.error = "This field is required";
                    errorCount++;
                } else if (e.value.length > e.max) {
                    e.error = `Maximum ${e.max} characters`;
                    errorCount++;
                } else if (e.value.length < e.min) {
                    e.error = `Minimum ${e.min} characters`;
                    errorCount++;
                }
            });
            return errorCount;
        },
    },
});
